import Dexie from 'dexie';

const collageDB = new Dexie('collage');

collageDB.version(1).stores({
  images:
    'title, src,originalSRC,originalHeight, originalWidth, naturalWidth,naturalHeight,aspectRatio,type,targetAspectRatio,file', // Primary key and indexed props
  details: '++id, details',
});

export default { collageDB };
